@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.portfolio {
    text-align: center;
    background-image: url(/portfolio/static/media/vizag1.c9f1a2f6.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 650px;
    padding: 70px;
}

.portfolio h1 {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 38px;
    font-weight: 500;
    letter-spacing: 2.5px;
}

.portfolio p {
    font-size: 16px;
    font-weight: 600;
    font-family: 'Prompt', sans-serif;
    color: rgb(59, 59, 59);
    letter-spacing: 2px;
}

.ProfilePic {
    height: 250px;
    width: 250px;
    border-radius: 50%;
    margin: 20px;
}

.cardHolder {
    padding: 50px;
    margin: 0;
}

.cardHolder .row {
margin: 0;
}

.Link {
text-decoration: none !important;
color: rgba(61, 61, 61, 0.945); 
}

.Link:hover {
    color: rgba(32, 32, 32, 0.945);
}

/*  */

.Displaycard {
    height: 400px;
    width: 360px;
    border: 1px solid rgb(209, 208, 208);
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgb(189, 189, 189);
    text-align: center;
    transition: 0.4s;
}

.Displaycard:hover {
    box-shadow: 1px 1px 20px rgb(150, 150, 150);
}

.Displaycard img {
    height: 280px;
    width: 350px;
    border-radius: 5px;
    margin: 5px 0 5px 0;
}

.about-me {
    padding: 50px;
    text-align: center;
    margin: 50px auto;
    width: 75%;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgb(180, 180, 180);
}

.about-me h2 {
    font-weight: 700;
    font-family: 'Josefin sans', sans-serif;
}

.about-me p {
    width: 75%;
    margin: 15px auto;
    font-weight: 500;
    text-shadow: 1px 1px rgb(224, 224, 224);
}

footer {
    display: -webkit-inline-flex;
    display: inline-flex;
    padding: 50px;
    background: rgba(0, 0, 15, 0.918);
    color: white;
    text-align: center;
    width: 100%;
}

footer p {
    margin: 20px;
}


@media screen and (max-width: 420px) {

    .portfolio {
        padding: 60px 30px 30px 30px;
        height: 750px;
    }

    .cardHolder {
        padding: 10px;
    }

    .Displaycard {
        width: 330px;
        height: 350px;
    }

    .Displaycard img {
        height: 220px;
        width: 320px;
    }

    .cardHolder .row .col {
        margin: 10px 0 10px 0;
    }

    .about-me {
        width: 90%;
        padding: 20px;
    }

    .about-me p {
        width: 100%;
    }
}
.navbar {
    color: white !important;
    margin-bottom: 150px !important;
}
.cyberPage {
    text-align: center;
    padding: 50px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 80%;
    border-radius: 5px;
    border: 1px solid rgba(240, 239, 239, 0.945);
    margin: 40px auto 20px auto;
    background: rgba(248, 248, 248, 0.932);
    box-shadow: 1px 1px 7px rgba(177, 176, 176, 0.938);
}

.cyberPage .info {
    width: 80%;
    margin: 30px auto 0 auto;
    text-align: left;
}

.question {
   border: 1px solid rgba(250, 244, 161, 0.945);
   box-shadow: 1px 1px 15px rgb(184, 183, 183);
   background: white;
   border-radius: 5px;
   padding: 10px;
   margin: 10px;
   width: 100%;
}

.question header {
    display: -webkit-inline-flex;
    display: inline-flex;
    width: 100%;
}

.question h4 {
    width: 90%;
}

.question p {
    width: 100%;
    transition: all 0.4s;
}

.question .btn {
  background: transparent;
  border-color: transparent;
  width: 2.5rem;
  height: 2.5rem;
  background: #eae6eb;
  /* font-size: 18px; */
  float: right !important;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
  color: #b4345c;
  cursor: pointer;
  margin-left: 1rem;
  -webkit-align-self: center;
          align-self: center;
  min-width: 2rem;
}

.question .btn .plus {
    font-size: 52px;
}

@media screen and (max-width: 420px) {
    .cyberPage {
        width: 95%;
        padding: 20px;
    }

    .cyberPage .info {
        width: 95%;
    }
}
.webdiv {
    text-align: center;
}

.webdiv h1 {
    padding-top: 30px;
}

.backBtn {
    height: 40px;
    width: 40px;
    padding: 0px 5px 0px 5px;
    font-size: 28px;
    float: left;
    top: 10px;
    margin-left: 20px;
    box-shadow: 1px 1px 6px rgb(204, 204, 204);
    border-radius: 5px;
}

.backBtn:hover {
    cursor: pointer;
}

.backIcon {
    margin-top: -8px;
}

@media screen and (max-width:420px) {
    .backBtn {
        margin-left: 5px;
    }
}
